import React from "react";
import { useSelector } from "react-redux";
import { useFirestoreConnect, useFirebase } from "react-redux-firebase";
import { useHistory } from "react-router-dom";
import "../../sass/blogs.scss";

const BlogPosts = () => {
  const { uid } = useSelector((state) => state.firebase.auth);
  const firebase = useFirebase();
  const history = useHistory();
  useFirestoreConnect({
    collection: `users/${uid}/blogs`,
    storeAs: "blogs",
  });
  const blogs = useSelector((state) => state.firestore.data.blogs);

  const signInWithGoogle = () => {
    firebase
    .login({
      provider: "google",
      type: "popup"
    })
    .then(() => {
      history.push("/profile");
    });
  };

  const handleOnClick = (blogTitle, blogImage, smallDescription, description) => {
    history.push({
      pathname: '/expandBlog',
      state: {
        title: blogTitle,
        image: blogImage,
        smallDesc: smallDescription,
        desc: description,
        isSignedIn: false
      }
    });
  }

  return (
    <div>
      <nav className="navbar navbar-expand-sm bg-dark navbar-dark fixed-top navContainer">
        <a href="#" className="navbar-brand"><strong>B</strong>log</a>
        <button className='btn btn-outline-info signInBtn' onClick={(event) => { event.preventDefault(); signInWithGoogle(); }}>SignIn</button>
      </nav>
      <div className="mainPageImg">
        <div className="imgContent">
          <h1>Create Your Own Blog</h1>
          <p>This platform gives you freedom to write your creative content, manage and develop it and publish it online.</p>
        </div>
      </div>
     
      <div className='container-fluid containerContent'>
        {blogs && Object.values(blogs).map((blog) => (
          <div>
            {blog === null ?
              ''
              :
              <div className="container cardContainer">
                <center>
                  <div className="card cardContent">
                    <img className="card-img-top cardImg" src={blog.image ? blog.image : 'https://images.unsplash.com/photo-1493934558415-9d19f0b2b4d2?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60'} alt="Card image"/>
                    <div className="card-body text-justify">
                      <h4 className="card-title">{blog.title}</h4>
                      <p>{blog.smallDescription}</p>
                      <a href="#" className='btn btn-primary stretched-link btn-lg' onClick={(event) => { event.preventDefault(); handleOnClick(blog.title, blog.image, blog.smallDescription, blog.description) }}>Read More</a>
                    </div>
                  </div>
                </center>
              </div>
            }
          </div>
        ))
        }
        {!blogs ? <div className='border border-0 bg-light notCreatedContent' style={{ padding: '40px', fontSize: '30px' }}>
                    Nothing Created Yet
                  </div> : ''}
      </div>
    </div>
  );
};
export default BlogPosts;