import React, { useState } from 'react';
import { useSelector } from "react-redux";
import { useLocation, useHistory } from 'react-router-dom';
import { useFirebase, useFirestore } from "react-redux-firebase";
import "../../../sass/editBlog.scss";

const EditBlogComponent = () => {
    const { displayName, uid } = useSelector((state) => state.firebase.auth);
    const location = useLocation();
    const { title, image, smallDesc, desc, blogID } = (location && location.state) || {}

    const firebase = useFirebase();
    const history = useHistory();
    const firestore = useFirestore();
    const [blogTitle, setBlogTitle] = useState(title);
    const [blogSmallDesc, setEditedValue] = useState(smallDesc);
    const [blogContent, setNewContent] = useState(desc);
    const [imgValue, setImg] = useState("");

    const handleInpChange = (event) => {
        setBlogTitle(event.target.value);
    }
    const smallDescChange = (event) => {
        if(event.target.name === "smallDesc"){
            setEditedValue(event.target.value);
        }
    }

    const handleImgChange = (event) => {
        if (event.target.files[0]) {
            setImg(event.target.files[0]);
        }
    }

    const handleDescriptionChange = (event) => {
        setNewContent(event.target.value);
    }


    const handleEdit = () => {
        firestore.collection("users").doc(uid).collection("blogs").doc(blogID).update({
            title: blogTitle,
            smallDescription: blogSmallDesc,
            image: imgValue.name || image,
            description: blogContent
        })
        history.push('/profile');
    }

    const signout = () => {
        firebase
            .auth()
            .signOut()
            .then(() => {
                history.push(process.env.PUBLIC_URL + "/");
            });
    };

    return (
        <div>
            <nav className="navbar navbar-expand-sm bg-dark navbar-dark navbarContainer">
                <a href="#" className="navbar-brand"><strong>B</strong>log</a>
                <div className='dropdown profileDropdown'>
                    <button className='btn btn-info rounded-circle profileIcon' data-toggle="dropdown">{displayName[0]}</button>
                    <div className="dropdown-menu dropdown-menu-right">
                        <a className="dropdown-item font-weight-bold" href="#">{displayName}</a>
                        <a className="dropdown-item" href="#" onClick={(event) => { event.preventDefault(); signout(); }}>SignOut</a>
                    </div>
                </div>
            </nav>
            <div className="container formContainer">
                <div className="header">
                    <center><h3>Edit Your Blog :</h3></center>
                </div>
                <form action="" onSubmit={(e) => { e.preventDefault(); handleEdit(); }}>
                    <table className="table table-borderless">
                        <tbody>
                            <tr>
                                <td>Title</td>
                                <td>
                                    <div className="form-group">
                                        <input type="text" className="form-control" id="title" placeholder="Enter title" name="title" value={blogTitle} onChange={handleInpChange} />
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <td>SmallDescription</td>
                                <td>
                                    <div className="form-group">
                                        <input type="text" className="form-control" rows="1" id="smallDesc" placeholder="Enter small description" name="smallDesc" value={blogSmallDesc} onChange={smallDescChange} />
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <td>Image</td>
                                <td>
                                    <div className="form-group">
                                        <input type="file" className="form-control-file border" id="img" name="img" onChange={handleImgChange} />
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <td>Description</td>
                                <td>
                                    <div className="form-group">
                                        <input type="text" className="form-control" rows="5" name="desc" value={blogContent} onChange={handleDescriptionChange} />
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <input type="submit" className="btn btn-success" value="PUBLISH" />
                </form>
            </div>
        </div>
    )
}
export default EditBlogComponent;