import React from "react";
import ReactDOM from "react-dom";
import firebase from "firebase/app";
import "firebase/auth";
import "firebase/firestore";
import { createStore } from "redux";
import { Provider } from "react-redux";
import { ReactReduxFirebaseProvider } from "react-redux-firebase";
import { createFirestoreInstance } from "redux-firestore";
import { rootReducer } from "./store/Reducer/reducers.js";
import App from "./App";
import { BrowserRouter } from "react-router-dom";

// For Firebase JS SDK v7.20.0 and later, measurementId is optional
const firebaseConfig = {
  apiKey: "AIzaSyD7tJwFEkbKUDwCOo0nyVMejQM9ecjo9n0",
  authDomain: "blogapp-7d6c5.firebaseapp.com",
  databaseURL: "https://blogapp-7d6c5.firebaseio.com",
  projectId: "blogapp-7d6c5",
  storageBucket: "blogapp-7d6c5.appspot.com",
  messagingSenderId: "777505948525",
  appId: "1:777505948525:web:b5802a0dd58793855f9064",
  measurementId: "G-PEWGRYT1ZF"
};

const rrfConfig = {
  userProfile: "users",
  useFirestoreForProfile: true,
};
firebase.initializeApp(firebaseConfig);
firebase.firestore();

const initialState = {};
const store = createStore(rootReducer, initialState);

const rrfProps = {
  firebase,
  config: rrfConfig,
  dispatch: store.dispatch,
  createFirestoreInstance, //since we are using Firestore
};

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
      <ReactReduxFirebaseProvider {...rrfProps}>
        <BrowserRouter basename={process.env.PUBLIC_URL}>
          <App />
        </BrowserRouter>
      </ReactReduxFirebaseProvider>
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);
